:root {
  --font-body: Arial, sans-serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5rem;
  max-width: 69.375rem;
  margin: 0 auto;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

@media (max-width: 71.8125em) {
  .header {
    background-image: url("../../../images/mobile/image-header.jpg");
    background-size: 100% 100%;
    background-repeat: no-repeat;
    height: 70vh;
  }
  .header__nav {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-top: 1.5625rem;
    margin-left: 0.9375rem;
    margin-right: 0.9375rem;
  }
  .header__heading {
    display: grid;
    grid-template-rows: repeat(2);
    justify-items: center;
    margin-top: 4.375rem;
  }
  .header__heading p {
    text-align: center;
    letter-spacing: 0.3125rem;
    font-size: 1.875rem;
    text-transform: uppercase;
    font-weight: 900;
    color: white;
    font-family: "Fraunces", serif;
  }
}
@media (min-width: 71.875em) {
  .header {
    background-image: url("../../../images/desktop/image-header.jpg");
    width: 100%;
    height: 100vh;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }
  .header__nav {
    padding-top: 1.25rem;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  .header__nav--image {
    margin-left: 2.5rem;
  }
  .header__nav--navbar button {
    margin-right: 3.125rem;
    width: 155px;
    height: 60px;
    text-transform: uppercase;
    font-size: 1.375rem;
    color: black;
    font-family: "Barlow", sans-serif;
    font-weight: 500;
    border-radius: 30px;
    border-style: none;
    background-color: white;
    cursor: pointer;
  }
  .header__nav--navbar a p {
    margin-right: 3.125rem;
    font-weight: 600;
    display: inline-block;
    font-size: 1.375rem;
    color: white;
    font-family: "Barlow", sans-serif;
  }
  .header__heading {
    margin-top: 6.25rem;
    display: grid;
    grid-template-rows: repeat(2);
    justify-items: center;
  }
  .header__heading p {
    color: white;
    text-transform: uppercase;
    font-family: "Fraunces", serif;
    font-size: 3.125rem;
    letter-spacing: 0.875rem;
  }
}

.flex {
  display: flex;
}
.flex__jc-sb {
  justify-content: space-between;
}
.flex__jc-c {
  justify-content: center;
}
.flex__jc-ai {
  align-items: center;
}
.flex__jc-end {
  justify-content: flex-end;
}

.grid {
  display: grid;
  grid-template-rows: repeat(2, 1fr);
}
.grid__ji-c {
  justify-items: center;
}
.grid__ai-c {
  align-items: center;
}
@media (min-width: 71.875em) {
  .grid {
    grid-template-columns: repeat(2, 1fr);
  }
}

@media (max-width: 71.8125em) {
  .hide-for-mobile {
    display: none;
  }
}

@media (min-width: 71.875em) {
  .hide-for-desktop {
    display: none;
  }
}

@media (max-width: 71.8125em) {
  .partition {
    display: grid;
    grid-template-rows: 1fr 1fr;
  }
  .partition__img {
    background-image: url("../../../images/mobile/image-transform.jpg");
    width: 100%;
    height: 50vh;
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }
  .partition__info {
    text-align: center;
    padding: 3.4375rem 1.25rem;
  }
  .partition__info .heading {
    font-size: 2rem;
    font-family: "Fraunces", serif;
  }
  .partition__info .txt {
    padding-left: 1.875rem;
    color: gray;
    line-height: 1.5625rem;
    font-size: 0.875rem;
    font-family: "Barlow", sans-serif;
  }
  .partition__info a {
    font-family: "Fraunces", serif;
    font-size: 0.75rem;
    color: black;
    position: relative;
    text-decoration: underline #fad400 3px;
  }
  .partition .order2 {
    order: 2;
  }
  .partition .order1 {
    order: 1;
  }
}
@media (min-width: 71.875em) {
  .partition {
    display: grid;
    align-items: center;
    justify-items: center;
    overflow: hidden;
    grid-template-columns: 1fr 1fr;
  }
  .partition__img img {
    vertical-align: top;
  }
  .partition__info .heading {
    font-size: 2rem;
    font-family: "Fraunces", serif;
  }
  .partition__info .txt {
    color: gray;
    line-height: 1.5625rem;
    font-size: 0.875rem;
    font-family: "Barlow", sans-serif;
  }
  .partition__info a {
    font-family: "Fraunces", serif;
    font-size: 0.75rem;
    color: black;
    position: relative;
  }
  .partition__info a::after {
    position: absolute;
    content: "";
    width: 100%;
    top: 1.25rem;
    transition: 300ms ease-in-out;
    height: 5px;
    display: block;
  }
  .partition__info a:hover::after {
    background-color: #fad400;
  }
}

